:root {
  --pri-font:"Inter", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --blue: hsl(202, 87%, 54%);
  --orange: hsl(14, 91%, 54%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  list-style: none;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--pri-font);
}

h1, h2, h3 {
  margin-top: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

@media (max-width: 63.9375em) {
  .hide-for-mobile {
    display: none !important;
  }
}

@media (min-width: 64em) {
  .hide-for-desktop {
    display: none !important;
  }
}

nav {
  background-color: transparent !important;
}
nav ul li {
  display: inline-block;
  position: relative;
}
nav ul li:not(:first-child).active::before {
  content: "";
  z-index: 2;
  position: absolute;
  left: 0;
  right: 0;
  bottom: -1rem;
  height: 2.3px;
  width: 100% !important;
  background-color: var(--white);
  opacity: 1;
  transition: all 0.3s ease-in-out;
}
nav ul li a {
  transition: color 0.2s ease-in-out !important;
}
nav ul li a:hover {
  color: rgb(237, 221, 221);
}
nav .mobileNav {
  top: -150%;
  transition: all 0.2s ease-in-out !important;
}
nav .mobileNav li {
  transition: color 0.2s ease-in-out !important;
}
nav .mobileNav li a {
  display: block !important;
  transition: color 0.2s ease-in-out !important;
}
nav .mobileNav li a:hover {
  color: rgb(92, 92, 92);
}

.hero {
  background-color: black;
  background-image: url(/images/home-bgimg.png);
  background-position: center center;
  height: 100vh;
  background-repeat: no-repeat;
  color: white;
}
.hero__container .mapCtn #map {
  width: min(100%, 25.5rem);
  height: 21.875rem;
}

